'use client'

import { useState, useEffect } from 'react'
import { useParams, useRouter } from 'next/navigation'
import Link from 'next/link'

// ==========================================
// TYPES
// ==========================================

interface FunnelStep {
  id: string
  name: string
  type: StepType
  path: string
  order: number
  status: 'draft' | 'published'
  views: number
  conversions: number
  conversionRate: number
  nextSteps: { stepId: string; condition?: string }[]
  template?: string
  content?: StepContent
}

interface StepContent {
  headline?: string
  subheadline?: string
  buttonText?: string
  buttonColor?: string
  backgroundColor?: string
  elements: ContentElement[]
}

interface ContentElement {
  id: string
  type: 'text' | 'image' | 'video' | 'form' | 'button' | 'countdown' | 'testimonial' | 'pricing'
  content: any
  position: { x: number; y: number }
  size: { width: number; height: number }
}

interface Funnel {
  id: string
  name: string
  description?: string
  folder: string
  status: 'draft' | 'published' | 'archived'
  domain?: string
  favicon?: string
  steps: FunnelStep[]
  createdAt: string
  updatedAt: string
  settings: FunnelSettings
  stats: FunnelStats
}

interface FunnelSettings {
  seoTitle?: string
  seoDescription?: string
  trackingCode?: string
  facebookPixel?: string
  googleAnalytics?: string
  customCss?: string
  customJs?: string
  redirectUrl?: string
}

interface FunnelStats {
  totalViews: number
  totalConversions: number
  overallRate: number
  revenue: number
}

type StepType = 
  | 'landing' 
  | 'sales' 
  | 'checkout' 
  | 'upsell' 
  | 'downsell' 
  | 'thankyou' 
  | 'webinar' 
  | 'optin' 
  | 'survey' 
  | 'quiz'
  | 'video-sales'
  | 'application'
  | 'calendar'

type TabType = 'steps' | 'settings' | 'analytics' | 'integrations'

